import React from 'react';
import { motion } from 'framer-motion';

const legalProcessSteps = [
  {
    id: 1,
    title: "Consulta Inicial",
    description: "Evaluamos su caso y discutimos las opciones legales disponibles.",
    icon: "🤝"
  },
  {
    id: 2,
    title: "Investigación y Análisis",
    description: "Recopilamos evidencia y analizamos los aspectos legales de su caso.",
    icon: "🔍"
  },
  {
    id: 3,
    title: "Estrategia Legal",
    description: "Desarrollamos una estrategia personalizada para su situación.",
    icon: "📊"
  },
  {
    id: 4,
    title: "Negociación o Litigio",
    description: "Representamos sus intereses en negociaciones o en la corte.",
    icon: "⚖️"
  },
  {
    id: 5,
    title: "Resolución",
    description: "Trabajamos para lograr la mejor resolución posible para su caso.",
    icon: "🏆"
  }
];

const LegalProcessInfographic = () => {
  return (
    <section className="py-16 bg-gray-100 dark:bg-gray-800">
      <div className="container mx-auto px-4">
        <h2 className="text-3xl font-bold text-center mb-12 dark:text-white">Nuestro Proceso Legal</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-5 gap-8">
          {legalProcessSteps.map((step) => (
            <motion.div
              key={step.id}
              className="bg-white dark:bg-gray-700 p-6 rounded-lg shadow-lg text-center"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              <div className="text-4xl mb-4" aria-hidden="true">{step.icon}</div>
              <h3 className="text-xl font-semibold mb-2 dark:text-white">{step.title}</h3>
              <p className="text-gray-600 dark:text-gray-300">{step.description}</p>
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default LegalProcessInfographic;