import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Link } from 'react-router-dom';
import SEO from '../components/SEO';
import Modal from '../components/Modal';
import LegalProcessInfographic from '../components/LegalProcessInfographic';
import { useTheme } from '../contexts/ThemeContext';
import videoSrc from '../assets/video.webm';

function Home() {
  const { darkMode } = useTheme();
  const [selectedService, setSelectedService] = React.useState(null);

  const services = [
    {
      title: "Derecho Corporativo",
      icon: "⚖️",
      description: "Asesoramiento integral en regulaciones corporativas y fusiones y adquisiciones.",
      details: "Nuestro equipo de expertos ofrece asesoramiento completo en derecho corporativo, abarcando áreas como fusiones y adquisiciones, gobierno corporativo, protección de datos, y cumplimiento normativo en el sector tecnológico y fintech."
    },
    {
      title: "Litigio y Resolución de Disputas",
      icon: "🏛️",
      description: "Representación experta en litigios complejos y arbitrajes.",
      details: "Brindamos asesoría legal especializada en derecho ambiental, incluyendo gestión de permisos, evaluación de impacto ambiental, litigios ambientales y desarrollo de estrategias de sostenibilidad corporativa."
    },
    {
      title: "Derecho Laboral",
      icon: "👥",
      description: "Asesoría en relaciones laborales y cumplimiento normativo.",
      details: "Ofrecemos servicios integrales de protección de propiedad intelectual, incluyendo patentes, marcas y derechos de autor, así como asesoramiento en transacciones comerciales internacionales y resolución de disputas transfronterizas."
    },
    {
      title: "Propiedad Intelectual",
      icon: "📜",
      description: "Protección de activos intangibles y gestión de derechos de autor.",
      details: "Nuestro equipo de expertos ofrece asesoramiento completo en derecho corporativo, abarcando áreas como fusiones y adquisiciones, gobierno corporativo, protección de datos, y cumplimiento normativo en el sector tecnológico y fintech."
    },
  ];

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        delayChildren: 0.3,
        staggerChildren: 0.2
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1
    }
  };

  const Button = ({ to, variant, children }) => (
    <Link
      to={to}
      className={`inline-block px-6 py-3 rounded-lg font-semibold text-lg transition-colors duration-300 ${
        variant === 'primary'
          ? 'bg-blue-600 text-white hover:bg-blue-700'
          : 'bg-white text-blue-600 hover:bg-gray-100'
      }`}
    >
      {children}
    </Link>
  );

  return (
    <>
      <SEO 
        title="Bufete Salas Granados - Asesoría Legal de Excelencia en Costa Rica"
        description="Bufete Salas Granados ofrece servicios legales de primer nivel en derecho corporativo, litigio, laboral y propiedad intelectual para empresas en Costa Rica."
        keywords="asesoría legal Costa Rica, derecho corporativo, litigio, derecho laboral, propiedad intelectual"
      />
      <div className={`${darkMode ? 'bg-gray-900 text-white' : 'bg-white text-gray-900'} font-serif`}>
        <motion.section 
          className={`${darkMode ? 'bg-gray-800' : 'bg-gray-100'} py-32 relative overflow-hidden`}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          <div className="container mx-auto px-4 relative z-10">
            <motion.h1 
              className="text-5xl md:text-7xl font-bold mb-6 text-center"
              initial={{ opacity: 0, y: -20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              Bufete Salas Granados
            </motion.h1>
            <motion.p 
              className="text-xl md:text-3xl mb-10 text-center"
              initial={{ opacity: 0, y: -20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.2 }}
            >
              Excelencia legal y compromiso con nuestros clientes
            </motion.p>
            <motion.div
              className="text-center"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.4 }}
            >
              <Button to="/contacto" variant={darkMode ? "secondary" : "primary"}>Solicitar Consulta</Button>
            </motion.div>
          </div>
          
          <motion.div
            className="absolute inset-0 w-full h-full"
            initial={{ opacity: 0, scale: 1.1 }}
            animate={{ opacity: 0.15, scale: 1 }}
            transition={{ duration: 1, delay: 0.5 }}
          >
            <video
              autoPlay
              loop
              muted
              playsInline
              className="w-full h-full object-cover"
            >
              <source src={videoSrc} type="video/webm" />
              Your browser does not support the video tag.
            </video>
          </motion.div>
        </motion.section>

        <section className="py-24">
          <div className="container mx-auto px-4">
            <motion.h2 
              className={`text-3xl md:text-4xl font-bold mb-16 text-center ${darkMode ? 'text-white' : 'text-gray-900'}`}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              Áreas de Práctica
            </motion.h2>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
              {services.map((service, index) => (
                <motion.div 
                  key={index}
                  className={`${darkMode ? 'bg-gray-800 hover:bg-gray-700' : 'bg-white hover:bg-gray-50'} rounded-lg shadow-lg p-6 text-center cursor-pointer transition-colors duration-300`}
                  variants={itemVariants}
                  initial="hidden"
                  animate="visible"
                  whileHover={{ scale: 1.03 }}
                  whileTap={{ scale: 0.98 }}
                  onClick={() => setSelectedService(service)}
                >
                  <div className="text-4xl mb-4" aria-hidden="true">{service.icon}</div>
                  <h3 className={`text-xl font-semibold mb-2 ${darkMode ? 'text-white' : 'text-gray-900'}`}>{service.title}</h3>
                  <p className={darkMode ? 'text-gray-300' : 'text-gray-600'}>{service.description}</p>
                </motion.div>
              ))}
            </div>
          </div>
        </section>

        <section className={`${darkMode ? 'bg-gray-800' : 'bg-gray-100'} py-24`}>
          <div className="container mx-auto px-4">
            <h2 className={`text-3xl md:text-4xl font-bold mb-16 text-center ${darkMode ? 'text-white' : 'text-gray-900'}`}>Por Qué Elegirnos</h2>
            <motion.ul 
              className="grid grid-cols-1 md:grid-cols-2 gap-10"
              variants={containerVariants}
              initial="hidden"
              animate="visible"
            >
              {[
                "Experiencia probada en casos complejos",
                "Enfoque personalizado y atención al detalle",
                "Compromiso con la excelencia y la ética profesional",
                "Red global de contactos y recursos legales",
              ].map((item, index) => (
                <motion.li 
                  key={index} 
                  className={`flex items-center ${darkMode ? 'bg-gray-700 text-white' : 'bg-white text-gray-800'} rounded-lg shadow p-6`}
                  variants={itemVariants}
                >
                  <span className={`${darkMode ? 'text-blue-400' : 'text-blue-500'} mr-4 text-2xl`} aria-hidden="true">✓</span>
                  <span className="text-lg">{item}</span>
                </motion.li>
              ))}
            </motion.ul>
          </div>
        </section>

        <LegalProcessInfographic />

        <section className="py-24">
          <div className="container mx-auto px-4 text-center">
            <h2 className={`text-3xl md:text-4xl font-bold mb-10 ${darkMode ? 'text-white' : 'text-gray-900'}`}>Proteja sus Intereses con Asesoría Legal de Excelencia</h2>
            <p className={`text-xl mb-10 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>Agende una consulta personalizada y descubra cómo podemos ayudarle a alcanzar sus objetivos legales.</p>
            <Button to="/contacto" variant={darkMode ? "secondary" : "primary"}>Agendar Consulta</Button>
          </div>
        </section>

        <AnimatePresence>
          {selectedService && (
            <Modal
              isOpen={!!selectedService}
              onClose={() => setSelectedService(null)}
              title={selectedService.title}
            >
              <p>{selectedService.details}</p>
              <div className="mt-4">
                <Button to="/servicios" variant="primary">Ver todos los servicios</Button>
              </div>
            </Modal>
          )}
        </AnimatePresence>
      </div>
    </>
  );
}

export default Home;